import React, { Fragment,useState } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux';
import {addEducation} from '../../actions/profile/profile'

const AddEducation = ({addEducation,history}) => {

    const [formData, setFormData] = useState({
        school:'',
        degree:'',
        fieldofstudy:'',
        from:'',
        current:false,
        to:'',
        description:''
    })

     //toggle current job
  const [toDateDisabled,toggleDisabled] = useState(false);

    //destructure formData to pull out the variables to be used
  const {school,degree,fieldofstudy,from,to,current,description} = formData

//write the onchange function to push the input value to the state using the setFormData
  const onChange = e => setFormData({
      ...formData, [e.target.name]:e.target.value
  })


    return (
        <Fragment>
    
      <h1 className="large text-primary">
        Add Your Education
      </h1>
      <p className="lead">
        <i className="fas fa-graduation-cap"></i> Add any school, bootcamp, etc that
        you have attended
      </p>
      <small>* = required field</small>
      <form className="form" onSubmit={e=>{
          e.preventDefault()
          addEducation(formData,history)
      }}>
        <div className="form-group">
          <input
            type="text"
            placeholder="* School or Bootcamp"
            name="school"
            required
            value={school} onChange={e => onChange(e)}

          />
        </div>
        <div className="form-group">
          <input
            type="text"
            placeholder="* Degree or Certificate"
            name="degree"
            required
            value={degree} onChange={e => onChange(e)}
          />
        </div>
        <div className="form-group">
          <input type="text" placeholder="Field Of Study" name="fieldofstudy"  value={fieldofstudy} onChange={e => onChange(e)} />
        </div>
        <div className="form-group">
          <h4>From Date</h4>
          <input type="date" name="from"  value={from} onChange={e => {
            onChange(e)
            
            }} />
        </div>
        <div className="form-group">
          <p>
            <input type="checkbox" name="current" checked={current} value={current} onChange={e => {
                //update the formData state with whatever is not what the current is
                setFormData({...formData,current: !current})
                toggleDisabled(!toDateDisabled)
                }}/>{' '} Current School or Bootcamp
          </p>
        </div>
        <div className="form-group">
          <h4>To Date</h4>
          <input type="date" 
                disabled={
              //enable or disable to date
              toDateDisabled ? 'disabled':''} 
              name="to" value={to} onChange={e => onChange(e)} />
        </div>
        <div className="form-group">
          <textarea
            name="description"
            cols="30"
            rows="5"
            placeholder="Program Description"
            value={description} onChange={e => onChange(e)}
          ></textarea>
        </div>
        <input type="submit" className="btn btn-primary my-1" />
        <a className="btn btn-light my-1" href="dashboard.html">Go Back</a>
      </form>
        </Fragment>
    )
}

AddEducation.propTypes = {
    addEducation: PropTypes.func.isRequired,
}

export default connect(null,{addEducation})(AddEducation)
