import React, {Fragment,useEffect} from 'react'
import PropTypes from 'prop-types'
import {connect}  from 'react-redux'
import Spinner from '../layout/Spinner'
import {getProfileById} from '../../actions/profile/profile'
import ProfileTop from './ProfileTop'
import ProfileAbout from './ProfileAbout'
import ProfileExperience from './ProfileExperience'
import {Link} from 'react-router-dom'
import ProfileEducation from './ProfileEducation'
import ProfileGithub from './ProfileGithub'

const Profile = (
    {
        getProfileById, 
        profile:{profile, loading}, 
        auth, 
        match
    }
    ) => {
    useEffect(() => {
        //match.params.id gets the id from the url
        getProfileById(match.params.id)
    }, [getProfileById, match.params.id] )
     
    return (
        <Fragment>
        { 
            profile === null || loading ? (<Spinner/>) :
            (
                <Fragment>  
                    <Link to="/profiles" className="btn btn-light">
                        Back to profiles
                    </Link>
                    {auth.isAuthenticated && auth.loading === false && auth.user._id === profile.user._id && (<Link to='/edit-profile' className='btn btn-dark'>
                        Edit profile
                    </Link>
                    )}
                    <div class="profile-grid my-1">
                        <ProfileTop profile={profile} />
                        <ProfileAbout profile={profile} />
                        <div className="profile-exp bg-white pp-2">
                            <h2 className="text-primary">
                                Experience
                            </h2>
                            {(profile.experience.length > 0) ? 
                            (
                                <Fragment>
                                    { profile.experience.map((experience,key)=>(
                                        <ProfileExperience key={experience.id} experience={experience} />
                                        ))
                                    }
                                </Fragment>

                            ) : (<h4> No experience </h4>)}

                        </div>
                        
                        <div className="profile-edu bg-white pp-2">
                            <h2 className="text-primary">
                                Education
                            </h2>

                            {(profile.education.length > 0) ? 
                            (
                                <Fragment>
                                    { profile.education.map((education)=>(
                                        <ProfileEducation key={education.id} education={education} />
                                        ))
                                    }
                                </Fragment>

                            ) : (<h4> No Education </h4>)}
                        </div>

                        {
                            profile.githubusername && (<ProfileGithub username={profile.githubusername}/>)
                        }

                    </div>
                </Fragment>
            )
         }
        </Fragment>
    )
}

Profile.propTypes = {

}

const mapStateToProps = state => ({
    profile: state.profile,
    auth: state.auth
})

//will be needing getProfileById action and 
export default connect(mapStateToProps,{getProfileById})(Profile)
